@charset "utf-8";

/** Variables **/

$accent-color: #4285f4;
$accent-color-hover: #3b5074;
$accent-color-active: #3daee9;
$accent-rgb: rgb(66 133 244 / 30%);

/* stylelint-disable-next-line value-keyword-case */
$font-family: Ubuntu, sans-serif;
$font-family-monospace: "Fira Code", monospace;
$upstream-font-family: "Google Sans", sans-serif;

/** General **/

:root {
  --accent-color: #{$accent-color} !important;
  --accent-color-hover: #{$accent-color-hover} !important;
  --accent-color-active: #{$accent-color-active} !important;
  --accent-rgb: #{$accent-rgb} !important
}

/** Fonts **/

* {
  font-family: $font-family !important
}

.markup-2BOw-j {
  pre,
  pre *,
  pre code,
  pre code *,
  code.inline {
    font-family: $font-family-monospace !important
  }
}

.contentColumnDefault-1VQkGM .ace_editor::before {
  /* font-family: $font-family-monospace !important; */
  display: none !important
}

/** Upstream font for title bar **/

.wordmark-2iDDfm.wordmarkWindows-1v0lYD {
  font-family: $upstream-font-family !important
}
